import React from 'react';
import PropTypes from 'prop-types';
import BlcGridBase from 'components/BlcGrid/BlcGridBase';
import { util } from 'public/styles';

const blcContainer = props => (
	<BlcGridBase size={util.blcContainerSize(props.size)} margin={props.margin}>
		{props.children}
	</BlcGridBase>
);

blcContainer.propTypes = {
	children: PropTypes.node,
	margin: PropTypes.string,
	size: PropTypes.oneOf([null, 'smallest', 'smaller', 'larger', `largest`])
}

blcContainer.defaultProps = {
	children: null,
	margin: '0',
	size: null
}

export default blcContainer;
