import React from 'react';
import PropTypes from 'prop-types';
import { color, size } from 'public/styles';

const blcBox = props => (
	<div>
		{props.children}

		<style jsx>{`
			background-color: ${props.background};
			border-color: ${props.borderColor};
			border-style: ${props.borderStyle};
			border-width: ${props.borderWidth}px;
			box-sizing: border-box;
			height: ${props.height};
			margin: ${props.margin} auto;
			overflow: ${props.overflow};
			padding: ${props.padding};
			text-align: ${props.textAlign};
		`}</style>
	</div>
);

blcBox.propTypes = {
	background: PropTypes.string,
	borderColor: PropTypes.string,
	borderStyle: PropTypes.oneOf(['solid', 'dotted', 'dashed']),
	borderWidth: PropTypes.number,
	height: PropTypes.oneOf(['0', '100%', 'fit-content']),
	margin: PropTypes.string,
	overflow: PropTypes.oneOf(['visible', 'hidden', 'scroll', 'auto']),
	padding: PropTypes.string,
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcBox.defaultProps = {
	background: color.smokeLight,
	borderColor: color.smokeDark,
	borderStyle: 'solid',
	borderWidth: 0,
	height: 'fit-content',
	margin: '0',
	overflow: 'visible',
	padding: size.marginSmaller,
	textAlign: 'left'
}

export default blcBox;
