import React, { useRef, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { size, util } from 'public/styles';

const BlcRow = props => {
	const ref = useRef(null);
	const [rowWidth, setRowWidth] = useState(0);

	const setGutter = util.blcColumnGutter(props.gutter);
	const setColumnCount = React.Children.count(props.children);

	useEffect(() => {
		setRowWidth(ref.current.offsetWidth);
	}, [rowWidth]);

	let setColumnTotal 	 = size.columns;
	let setGutterCount 	 = setColumnCount - 1;
	let setColumnGutter	 = setColumnCount <= 1 ? 0 : setGutter * setGutterCount;
	let setTotalRowWidth = rowWidth - setColumnGutter;

	return (
		<div className={props.gutter} ref={ref}>
			{React.Children.map(props.children, (child) => React.cloneElement(child,
				{
					columnGutter: 	setGutter,
					columnTotal: 	setColumnTotal,
					totalRowWidth: 	setTotalRowWidth
				}))
			}

			<style jsx>{`
				align-content: ${props.alignContent};
				align-items: ${props.alignItems};
				display: flex;
				flex-direction: ${props.direction};
				flex-flow: ${props.wrap};
				flex-wrap: ${props.wrap};
				justify-content: ${props.justifyContent};
				margin: ${props.margin} auto;
				width: 100%;
			`}</style>
		</div>
	)
};

// ${util.blcColumnGutter(props.gutter)}px

BlcRow.propTypes = {
	alignContent: PropTypes.oneOf(['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly', 'stretch']),
	alignItems: PropTypes.oneOf(['stretch', 'flex-start', 'flex-end', 'center']),
	direction: PropTypes.oneOf(['row', 'row-reverse']),
	gutter: PropTypes.oneOf([null, 'smallest', 'smaller', 'inherit', 'larger', 'largest']),
	justifyContent: PropTypes.oneOf(['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly']),
	margin: PropTypes.string,
	wrap: PropTypes.oneOf(['nowrap', 'wrap', 'wrap-reverse'])
}

BlcRow.defaultProps = {
	alignContent: 'stretch',
	alignItems: 'stretch',
	direction: 'row',
	gutter: null,
	justifyContent: 'flex-start',
	margin: '0',
	wrap: 'wrap'
}


export default BlcRow;
