import React from 'react';
import PropTypes from 'prop-types';

const blcColumn = props => (
	<div className={'col-' + props.col}>
		{props.children}

		<style jsx>{`
			div {
				align-self: ${props.align};
				order: ${props.order};
				margin: ${props.margin} ${props.columnGutter / 2}px;
				width: ${props.totalRowWidth / props.columnTotal * props.col}px;
			}

			div:first-of-type { margin-left: 0; }
			div:last-of-type { margin-right: 0; }
		`}</style>
	</div>
);

blcColumn.propTypes = {
	align: PropTypes.oneOf(['auto', 'stretch', 'flex-start', 'flex-end', 'center']),
	col: PropTypes.number,
	children: PropTypes.node,
	margin: PropTypes.string,
	order: PropTypes.number,
	columnGutter: PropTypes.number.isRequired,
	totalRowWidth: PropTypes.number.isRequired,
	columnTotal: PropTypes.number.isRequired
}

blcColumn.defaultProps = {
	align: 'auto',
	col: 12,
	children: null,
	margin: '0',
	order: 0
}

export default blcColumn;
