import React from 'react';
import PropTypes from 'prop-types';
import { size } from 'public/styles';

const blcGridBase = props => (
	<div>
		{props.children}

		<style jsx>{`
			box-sizing: border-box;
			margin: ${props.margin} auto;
			max-width: ${props.size};
			padding: 0 ${size.marginSmaller};
			width: 100%;
		`}</style>
	</div>
);

blcGridBase.propTypes = {
	margin: PropTypes.string,
	size: PropTypes.string
}

blcGridBase.defaultProps = {
	margin: '0',
	size: '100%'
}

export default blcGridBase;
